
.currentlist {
    margin-bottom: 1.4em;
    .heading {
        border-bottom: var(--border-line);
        margin-top: 1em;
        margin-bottom: 2em;
        padding-bottom: .25rem;
        display: flex;
        align-items: baseline;
        .headsub {
            font-size: 1.5em; }
        h6 {
            font-weight: 500;
            margin-left: auto; } }
    .listing {
        list-style: none; }
    .row {
        display: grid;
        grid-gap: 2em;
        grid-template-columns: 7rem auto;
        margin-bottom: 2em;
        .thumb {
            height: 7rem;
            img {
                height: 7rem; } } }
    a:not(.button) {
        color: var(--text); }
    .title {
        font-size: 1.05rem; }
    .leads {
        font-size: .85rem; } }
