
.datelist {
    margin-top: 1.4em;
    margin-bottom: 1.4em;
    .heading {
        border-bottom: var(--border-line);
        margin-top: 1em;
        margin-bottom: 2em;
        padding-bottom: .25rem;
        display: flex;
        align-items: baseline;
        .headsub {
            font-size: 1.5em; }
        h6 {
            font-weight: 500;
            margin-left: auto; } }
    .listing {
        list-style: none; }
    .row {
        display: grid;
        grid-gap: 1em;
        margin-bottom: 2em;
        .thumbdate {
            align-items: center;
            justify-items: center;
            display: grid;
            .box {
                border-right: 1px solid var(--text);
                width: 5rem;
                height: 5rem;
                .date {
                    font-weight: 600;
                    font-size: 1.7rem; }
                .month {
                    font-size: 1.2rem; } } } }
    .years {
        .row {
            grid-template-columns: 15rem auto;
            .desc {
                border-right: var(--border-line);
                padding-right: 1em;
                margin-right: 1em; }
            .sublist {
                list-style: none;
                .links {
                    display: flex;
                    align-items: center;
                    .date {
                        font-weight: 600;
                        font-size: 1.5rem;
                        border-right: var(--border-line);
                        width: 3rem;
                        padding-right: 1em;
                        margin-right: 1em; }
                    .title {
                        font-weight: 400;
                        font-size: .99rem; } } } } }
    .month.first {
        width: 100%;
        border-bottom: var(--border-line);
        margin-bottom: 3em;
        .row {
            grid-template-columns: auto 50%;
            align-items: center;
            .title {
                font-size: 1.5em;
                font-weight: 600;
                text-transform: uppercase;
                margin-bottom: 1em; }
            .thumb {
                height: 18rem;
                img {
                    height: 18rem;
                    border-radius: 10px; } } } }
    .month.all {
        width: 45%;
        float: left;
        margin-right: 5%;
        .row {
            grid-gap: 1em;
            grid-template-columns: 6rem auto;
            .thumb {
                height: 6rem;
                img {
                    height: 11rem;
                    border-radius: 10px; } } } }
    a:not(.button) {
        color: var(--text); }
    .title {
        font-size: 1.05rem; }
    .leads {
        font-size: .85rem; } }
